            let _this = this;
            console.log(e);
            if (e.mp.detail.type == 'touch') {
                _this.current = e.mp.detail.current;
            }

            if (_this.current == 0) {
                _this.videoContext.play();
            }
        },
        touchStart(e) {
            let _this = this;
            console.log(e);
            _this.startX = e.clientX;
        },
        touchMove(e) {
            let _this = this;
            _this.offsetX = Math.abs(e.clientX - _this.startX);
            console.log(_this.offsetX);
        },
        touchEnd(e) {
            let _this = this;
            if (_this.offsetX > 20) {
                _this.videoContext.pause();
                _this.current = 1;
            }
        },
